.page-about-me {
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}



.page-about-me-container-info{
    display: flex;
    justify-content: space-between;
    width: 100%;
    min-height: 600px;
    @media only screen and (max-width: 600px) {
        flex-direction: column;
    }
    .photo {
        margin-left: 25px;
        border-radius: 10px;
        object-fit: cover;
        object-position: right;
        width: 35%;
        
    }

    .page-about-me-cover-letter {
        width: 55%;
        @media only screen and (max-width: 600px) {
            width: 100%;
        }
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h1 {
            font-family: 'IBM';
            font-weight: 400;
            margin-bottom: 0px;
        
        }
        
        .page-about-me-text-background {
            width: 100%;
            /* Full width of the parent container */
            height: 100%;
            /* Full height of the parent container */
            max-width: 100%;
            /* Ensure it doesn’t exceed parent width */
            max-height: 100%;
            /* Ensure it doesn’t exceed parent height */
            overflow: hidden;
            /* Hide overflow to avoid scrollbars */
            box-sizing: border-box;
            /* Include padding and border in the element’s total width and height */
        }
        
        .syntax-highlighter {
            width: 100%;
            
            overflow: auto;
            white-space: pre-wrap;
            /* Preserve whitespace and wrap long lines */
            box-sizing: border-box;
        }
        
        
        .page-about-me-code-about {
            margin-top: 0px;
            padding-top: 0px;
        }
    }
    
   
}



.informations {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-self: start;
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    .informations-skills {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .skills {
        margin-bottom: 5vh;
        display: flex;
        justify-content: center;
        /*.code-small {
        font-family: 'IBM';
        font-weight: lighter;
        font-size: 20px;
        }
        */
        .code {
            font-family: 'IBM';
            font-weight: lighter;
        
        }
    
    }
    
    
    .popup {
        display: flex;
        justify-content: center;
    
        background-color: rgba(0, 0, 0, 0.8);
        color: #fff;
        padding: 8px;
        border-radius: 5px;
        z-index: 1;
    
        width: 450px;
        height: 95%;
        
        ul {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            font-family: 'IBM';
            font-weight: normal;
            
        }
        li {
    
            font-size: 18px;
            margin-bottom: 10px;
            list-style: none;
            font-family: 'IBM';
            @media only screen and (max-width: 600px) {
                font-size: 0.7rem;
             }
        }
        .skill-title {
            display: flex;
        
            justify-content: center;
            text-transform: capitalize;
            .skill-title-head {
                display: flex;
                align-items: center;
                justify-content: center;
                img {
                    cursor: pointer;
                    width: 25px;
                    height: 25px;
                }
                .cursorPointer:hover {
                    cursor: pointer;
                }
            }
            p {
                margin-top: 0px;
                margin-bottom: 0px;
            
            }
        }
        
    
    }
    

    
    .icons {
    
        max-width: 70%;
        display: flex;
        flex-wrap: wrap;
        .row {
            flex-basis: 100%;
            display: flex;
            justify-content: space-around;
        
            margin-bottom: 5vh;
            .program-icon {
        
                
                max-width: 20%;
                transition: transform 0.15s ease-in-out, brightness 0.15s ease-in-out;
                @media only screen and (max-width: 600px) {
                    max-width: 40%;
                 }

                &:hover{
                    cursor: pointer;
                }

               &.pressed {
                transform: scale(0.80);
            
                filter: brightness(0.6);
            
            }
            
            }
            
           
        
        }
       
    
    }
    
    
    
    
    
    
    
   

}



@media only screen and (max-width: 1081px) {
    .page-about-me {
        /*new*/

        align-items: flex-start;
        flex-direction: column;
        justify-content: center;
        position: relative;

    }

    .syntax-highlighter {
        font-size: 5px;
    }



    .react-syntax-highlighter-line-number {
        width: 10px;
    }

    .photo {
        margin-left: 0px;
        align-self: center;
        max-width: 250px;
    }

    .page-about-me-cover-letter {
        align-self: center;
        width: 90%;
    }

    .page-about-me-cover-letter h1 {
        font-size: 20px;
    }

    .page-about-me-cover-letter p {
        font-size: 10px;
    }

    .informations {
        max-width: 95%;
        flex-direction: column-reverse;
        margin-left: 5vw;
        margin-right: 0px;
        margin-top: 0px;
    }

    .skills {
        width: 35vw;
        margin-bottom: 0vw;
    }

    .code {
        font-size: larger;
    }

    .program-icon {
        margin-right: 20px;
        max-width: 10vw;

    }



    .informations-pop-up {
        align-self: center;
        max-width: 98%;
    }

    .popup {

        max-width: 88%;
        max-height: 400px;
    }

    .popup li {
        font-size: 12px;
    }

    .informations-skills {
        align-self: center;
    }



}